.verify-container {
  height: calc(100vh - 423px);
  padding-top: 80px;
  width: 1200px;
  margin: 0 auto 100px;

  .col1 {
    width: 315px;
    display: inline-block;
    vertical-align: top;
  }

  .col1 > h3 {
    font-weight: bold;
    font-size: 28px;
    line-height: 1.29;
    letter-spacing: -0.56px;
    color: #222222;
  }

  .col2 {
    width: 885px;
    display: inline-block;
  }

  .col2 > .info {
    width: 100%;

    &::before {
      background: url('/assets/images/icon/locked-padlock.png') center no-repeat;
      content: '';
      width: 18px;
      height: 25px;
      display: inline-block;
      vertical-align: middle;
    }
  }

  .col2 > .info > p {
    height: 25px;
    vertical-align: middle;
    margin-left: 10px;
    font-size: 16px;
    line-height: 1.63;
    letter-spacing: -0.64px;
    color: #222222;
    display: inline-block;
  }

  .col2 > form {
    padding-top: 33.5px;
  }

  .col2 > form > input {
    width: 465px;
    padding: 14px 0;
    font-size: 16px;
    font-weight: 300;
    letter-spacing: -0.64px;
    color: #222222;
    border-bottom: 1px solid #cccccc;
    display: block;
  }

  .col2 > form > button {
    width: 120px;
    height: 52px;
    background-color: #e66526;
    margin-top: 47.5px;
    font-size: 16px;
    letter-spacing: -0.32px;
    color: #ffffff;
  }
}
