.single-comment-container {
  border-bottom: 1px solid #dddddd;
  padding: 20px 0;
  legend {
    font-size: 28px;
    font-weight: bold;
    line-height: 1.29;
    letter-spacing: -0.56px;
    padding-bottom: 20px;
  }
  textarea {
    width: 100%;
    padding: 10px;
    border: 1px solid #cccccc;
    resize: none;
    &.comment-modify-textarea {
      height: 100px;
    }
    &.recomment-modify-textarea {
      height: 52px;
      line-height: 1.9;
    }
  }
  .comment-date {
    font-size: 14px;
    line-height: 1.71;
    letter-spacing: -0.28px;
    color: #666666;
    margin-left: 10px;
  }
  .comment-more-botton {
    font-size: 14px;
    line-height: 1.71;
    letter-spacing: -0.28px;
    color: #666666;
    text-decoration: underline;
  }
  .comment-more-botton.opened:before {
    content: '▲ ';
  }
  .comment-more-botton.closed:before {
    content: '▼ ';
  }
  .all-comments-container {
    margin-top: 20px;
    margin-bottom: 10px;
    border: solid 1px #dddddd;
  }
  .comment-time:before {
    content: '|';
    margin: 0 10px 2px 10px;
    color: #cccccc;
    font-size: 12px;
  }
}
