@import url('https://fonts.googleapis.com/css2?family=Noto+Sans+KR:wght@300;400;500;700&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Lato:wght@300;400&display=swap');
@import 'table.css';
@import 'donation.css';

body {
  font-family: 'Noto Sans KR', sans-serif;
  color: #222222;
}

input[type='submit'] {
  cursor: pointer;
}
input::placeholder {
  color: #666;
}
textarea::placeholder {
  color: #666;
}
button {
  appearance: none;
  -moz-appearance: none;
  -webkit-appearance: none;
}

button span {
  position: relative;
}

a:focus,
button:focus {
  /* outline: 0; */
}
select:focus {
  border: 0;
  /* outline: 0; */
}

.skip-navigation a {
  position: absolute;
  top: -50px;
  left: 0;
  z-index: 5000;
  width: 100%;
  padding: 10px 0;
  background-color: rgba(0, 0, 0, 0.6);
  color: #fff;
  font-size: 15px;
  text-shadow: 1px 1px 0 rgba(0, 0, 0, 0.5);
  text-align: center;
}

.skip-navigation a:focus,
.skip-navigation a:hover,
.skip-navigation a:active {
  top: 0;
}

/* IE11 */
select::-ms-expand {
  display: none;
}

input::-ms-clear {
  display: none;
  height: 0;
  width: 0;
}
:-ms-input-placeholder {
  color: #ccc !important;
  font-weight: 400 !important;
}
img {
  -ms-interpolation-mode: bicubic;
}
/* IE11 */

/* 스크린리더기에는 인식하나 화면에서 보이기 않도록 함 */
.accessible-hidden,
.hidden,
caption {
  position: absolute;
  width: 1px;
  height: 1px;
  padding: 0;
  margin: -1px;
  overflow: hidden;
  clip: rect(0, 0, 0, 0);
  border: 0 none;
}

address {
  font-style: normal;
}

.text-ellipsis {
  text-overflow: ellipsis;
  overflow: hidden;
  white-space: nowrap;
}

.text-orange {
  color: #e66526;
}

.orange-button-filled {
