/* 커스텀 체크박스 */

label.checkbox-wrapper {
  display: inline-block;
  position: relative;
  padding-left: 35px;
  cursor: pointer;
  font-size: 22px;
  -webkit-user-select: none;
  -ms-user-select: none;
  user-select: none;
}

.checkbox-wrapper input {
  position: absolute;
  opacity: 0;
  cursor: pointer;
  height: 0;
  width: 0;
}

.checkbox-wrapper .checkmark {
  position: absolute;
  top: 4px;
  left: 0;
  height: 20px;
  width: 20px;
  background-color: #fff;
  border: 1px solid #cccccc;
}

.checkbox-wrapper input:checked ~ .checkmark {
  background-color: #fff;
  border: 1px solid #e66526;
}

.checkbox-wrapper .checkmark:after {
  content: '';
  position: absolute;
  display: none;
}

.checkbox-wrapper input:checked ~ .checkmark:after {
  display: block;
}

.checkbox-wrapper .checkmark:after {
  left: 5px;
  top: 1px;
  width: 7px;
  height: 13px;
  border: solid #e66526;
  border-width: 0 3px 3px 0;
  -webkit-transform: rotate(45deg);
  transform: rotate(45deg);
}

/*CHECKBOX DESIGN*/

.checkbox {
  display: flex;
  position: relative;
}

.checkbox input[type='checkbox'] {
  position: absolute;
  width: 1px;
  height: 1px;
  padding: 0;
  margin: -1px;
  overflow: hidden;
  clip: rect(0, 0, 0, 0);
  border: 0;
}

.checkbox input[type='checkbox']:focus + label::before {
  outline: 2px solid #a6c7ff;
}

.checkbox input[type='checkbox'] + label {
  color: #222;
  display: inline-block;
  position: relative;
  cursor: pointer;
  line-height: 24px;
  font-size: 16px;
}

.checkbox.bold input[type='checkbox'] + label {
  font-weight: bold;
}

.checkbox input[type='checkbox'] + label::before {
  content: ' ';
  box-sizing: border-box;
  display: inline-block;
  width: 24px;
  height: 24px;
  line-height: 24px;
  margin: -2px 10px 0 0;
  text-align: center;
  vertical-align: middle;
  background: #fff;
  border: 1px solid #ccc;
}

.checkbox input[type='checkbox'] + label::after {
  display: none;
  content: '';
  left: 8px;
  top: 4px;
  width: 8px;
  height: 14px;
  border: solid #e66526;
  border-width: 0 3px 3px 0;
  -webkit-transform: rotate(45deg);
  transform: rotate(45deg);
  position: absolute;
}

.checkbox input[type='checkbox']:checked + label::before {
  border: 1px solid #e66526;
}

.checkbox input[type='checkbox']:checked + label::after {
  display: block;
}

/* 커스텀 스크롤바 */

::-webkit-scrollbar {
  width: 8px;
  height: 8px;
}

::-webkit-scrollbar-thumb {
  background: #aaaaaa;
  border-radius: 10px;
}

/* 스크롤시 네비게이션 픽스*/

.sticky-80 {
  position: fixed;
  top: 80px;
  width: 100%;
  z-index: 6;
  box-shadow: 0 3px 10px 0 rgb(0 0 0 / 5%);
}

.sticky-0 {
  position: fixed;
  top: 0px;
  width: 100%;
  z-index: 6;
  box-shadow: 0 3px 10px 0 rgb(0 0 0 / 5%);
}

/* Animations */

@keyframes fadein {
  from {
    opacity: 0;
    -webkit-transform: translateY(30px);
    transform: translateY(30px);
  }

  to {
    opacity: 1;
    -webkit-transform: translateY(0px);
    transform: translateY(0px);
  }
}

@-webkit-keyframes fadein {
  /* Safari and Chrome */

  from {
    opacity: 0;
  }

  to {
    opacity: 1;
  }
}

@keyframes translateY {
  from {
    -webkit-transform: translateY(30px);
    transform: translateY(30px);
  }

  to {
    -webkit-transform: translateY(0px);
    transform: translateY(0px);
  }
}

@-webkit-keyframes translateY {
  /* Safari and Chrome */

  from {
    -webkit-transform: translateY(30px);
  }

  to {
    -webkit-transform: translateY(0px);
  }
}

