.project-title {
  .logo-area {
    width: 300px;
    height: 125px;
    overflow: hidden;
    display: -ms-flexbox;
    -ms-flex-align: center;
    -ms-flex-pack: center;
    background-repeat: no-repeat;
    background-size: contain;
    background-position: center;
  }
  .logo-area img {
    display: none;
  }
  .title-container {
    width: 1200px;
    height: 200px;
  }
  h3 {
    font-size: 32px;
    letter-spacing: -1.28px;
    font-weight: 300;
    margin-left: 40px;
    word-break: keep-all;
  }
}
