.alarm-list-item {
  width: 100%;
  border-bottom: 1px solid #dddddd;

  &:hover {
    background-image: linear-gradient(to bottom, rgba(34, 34, 34, 0.04), rgba(34, 34, 34, 0));
  }

  a {
    width: 100%;
    height: 100%;
    padding: 20px 0;
    cursor: pointer;
    display: inline-block;

    p {
      text-align: left;
      text-overflow: ellipsis;
      white-space: nowrap;
      overflow: hidden;
      font-size: 16px;
      line-height: 1.63;
      letter-spacing: -0.32px;
      text-align: left;
      color: #222222;
    }

    & > span {
      font-family: 'Lato';
      font-size: 14px;
      line-height: 1.43;
      color: #666666;
      &:nth-child(3):before {
        margin: 0 10px;
        content: '|';
        color: #cccccc;
      }
    }
  }

  .marker {
    width: 6px;
    height: 6px;
    margin-right: 4px;
    margin-bottom: 8px;
    background-color: #e66526;
    border-radius: 100%;
    display: inline-block;
  }
}
