.header {
  width: 100%;
  height: 80px;
  background-color: #f5f5f5;
  min-width: 1200px;
  display: flex;

  .header-pad {
    height: 80px;
    flex: 1;
  }

  .header-pad.left {
    background-color: #ffffff;
  }

  .header-pad.right {
    background-color: #f5f5f5;
  }

  .header-content {
    width: 1200px;
    height: 80px;
    display: flex;
  }

  .header-content-left {
    height: 80px;
    padding-right: 40px;
    background-color: #ffffff;
    display: flex;
  }

  .diagonal {
    width: 0px;
    height: 0px;
    display: inline-block;
    border-top: 80px solid #f5f5f5;
    border-left: 45px solid #ffffff;
  }

  .header-content-right {
    height: 80px;
    flex: 1;
  }
}
