.beneficiary-dialog {
  button:disabled,
  button[disabled] {
    color: #999999;
    cursor: initial;
  }
  h5 b {
    font-family: 'Lato';
    font-size: 32px;
    font-weight: 500;
    line-height: 1.44;
    margin-right: 10px;
  }
  h5 span {
    font-size: 32px;
    font-weight: 300;
    line-height: 1.44;
    letter-spacing: -1.28px;
  }
  dt {
    font-size: 20px;
    font-weight: bold;
    line-height: 1.3;
    letter-spacing: -0.4px;
    margin-top: 20px;
    margin-bottom: 10px;
  }
  dd {
    margin-bottom: 25px;
  }

  .tbl-scroll tbody {
    width: 1152px;
    height: 160px;
    overflow: auto;
    display: block;

    > tr > td {
      width: 384px;
    }
  }

  .btn-close {
    position: absolute;
    right: 20px;
    top: 20px;
  }
}
