.terms-container {
  width: 100%;

  .background {
    position: fixed;
    left: 0;
    top: 0;
    bottom: 0;
    z-index: -1;

    .mark-container {
      width: 360px;
      height: 100vh;
      display: inline-block;
      background-color: #eeeeee;
      position: relative;

      &::before,
      &::after {
        content: '';
        display: inline-block;
        position: absolute;
      }

      &::before {
        background: url('/assets/images/background/bg-question-mark.png') no-repeat center;
        background-size: contain;
        width: 255px;
        height: 395px;
        top: -32px;
        left: 32px;
      }

      &::after {
        background: url('/assets/images/background/bg-exclamation-point.png') no-repeat center;
        background-size: contain;
        width: 108px;
        height: 352px;
        bottom: 60px;
        right: 20px;
      }
    }
    .diagonal {
      width: 0px;
      height: 0px;
      display: inline-block;
      border-bottom: 100vh solid #ffffff;
      border-left: 115px solid #eeeeee;
    }
  }

  .contents-container {
    margin-left: 475px;
    padding: 100px 0 30px 200px;

    .contents-body {
      width: 885px;

      .logo {
        width: 215px;
        height: 40px;
      }

      h2 {
        margin-top: 25px;
        margin-bottom: 40px;
        font-family: 'Noto Sans KR', sans-serif;
        font-size: 48px;
        font-weight: 300;
        color: #222222;
      }

      .terms-form {
        padding: 20px 0;
        border-color: #ccc;
        border-style: solid;
        border-width: 2px 0 2px 0;
        section {
          padding: 20px 0;
          border-top: 1px solid #cccccc;

          &:first-child {
            border: none;
            padding-top: 0;
          }

          &:last-child {
            padding-bottom: 0;
          }

          label.checkbox-wrapper {
            font-family: 'Noto Sans KR', sans-serif;
            font-size: 16px;
            font-weight: normal;
            font-stretch: normal;
            font-style: normal;
            line-height: 1.63;
            letter-spacing: -0.32px;
            text-align: left;
            color: #222222;
          }

          .contents {
            width: 100%;
            height: 160px;
            margin-top: 20px;
            border: 1px solid #ddd;
            display: block;
            position: relative;
            overflow-y: auto;
            background-color: #fff;

            > .service-container {
              height: inherit;
              padding: 20px;
              margin: 0;
            }
          }
        }
      }

      .button-container {
        margin-top: 30px;
        text-align: center;

        button {
          width: 180px;
          height: 52px;
          border: 1px solid #e66526;
          font-family: 'Noto Sans KR', sans-serif;
          font-size: 16px;
          line-height: 2.25;
          letter-spacing: -0.32px;

          &:last-child {
            margin-left: 10px;
          }

          &.agree {
            background-color: #e66526;
            color: #ffffff;
          }

          &.disagree {
            color: #e66526;
          }
        }
      }
    }
  }
}
