.edit-container {
  width: 1200px;
  margin: 0 auto;
  padding-top: 30px;
  .label {
    display: inline-block;
    height: 52px;
    font-size: 20px;
    font-weight: bold;
    line-height: 52px;
    letter-spacing: -0.4px;
  }
  form > ul > li {
    display: grid;
    grid-template-columns: 100px 1fr;
    margin-bottom: 20px;
  }
  .fileList {
    border: solid 1px #cccccc;
    margin-top: 10px;
  }
  .fileList-name {
    height: 25px;
    background-image: url(/assets/images/icon/folder.png);
    background-repeat: no-repeat;
    background-size: 13px 13px;
    background-position-y: 30%;
    padding-left: 20px;
    font-size: 14px;
  }
  .fileList-remove-button {
    width: 15px;
    height: 15px;
    margin-left: 10px;
  }
}
