.post-container {
  width: 1200px;
  margin: 0 auto;
  margin-top: -10px;
  .post-desc {
    font-size: 14px;
    line-height: 1.71;
    letter-spacing: -0.28px;
    padding-bottom: 30px;
  }
  .post-desc p:after {
    content: '|';
    color: #aaaaaa;
    margin: 0 10px;
    font-size: 12px;
  }
  .post-content {
    padding-top: 40px;
    padding-bottom: 40px;
    overflow: hidden;

    a {
      text-decoration: underline;
      color: #0782c1;

      &:visited {
        color: #660099;
      }
    }
    ul {
      display: block;
      margin-block-start: 1em;
      margin-block-end: 1em;
      margin-inline-start: 0px;
      margin-inline-end: 0px;
      padding-inline-start: 40px;
      display: list-item;
      text-align: -webkit-match-parent;
      li {
        list-style: disc;
      }
    }
    ol {
      display: block;
      margin-block-start: 1em;
      margin-block-end: 1em;
      margin-inline-start: 0px;
      margin-inline-end: 0px;
      padding-inline-start: 40px;
      li {
        list-style: decimal;
      }
    }
  }
  .file-area {
    display: table;

    li {
      margin-right: 10px;
      float: left;

      a {
        width: auto;
        display: inline-block;
      }
    }
  }
  .sun-editor {
    border: none;
  }
}
