.table-area {
  padding-bottom: 100px;
}

table {
  border-collapse: collapse;
  letter-spacing: 1px;
  font-size: 16px;
  border-bottom: solid 2px #cccccc;
}

thead > tr > th {
  background-color: #f5f5f5;
  font-weight: bold;
  line-height: 1.63;
  letter-spacing: -0.32px;
  text-align: center;
  color: #666666;
  border-top: solid 2px #cccccc;
}

tbody > tr > td {
  border-top: solid 1px #dddddd;
}

tbody > tr > th {
  border-top: solid 1px #dddddd;
}

td,
th {
  padding: 10px 10px;
}

td {
  text-align: center;
}

tfoot > tr {
  border-top: solid 2px #cccccc;
  border-bottom: solid 2px #cccccc;
  font-weight: bold;
}

caption {
  padding: 10px;
}

table .body-scroll tbody {
  overflow-y: auto;
  height: 180px;
}

.body-scroll table {
  table-layout: fixed;
  border-spacing: 1px;
}

.body-scroll table th {
  top: -1px;
  position: sticky;
}
