.intro-wrap {
  .intro-top-wrapper {
    width: 100%;
    padding: 60px 0;
    background: url('/assets/images/background/bg-intro.svg');
    background-position: center;
    background-size: cover;

    .content-wrapper {
      width: 1200px;
      margin: 0 auto;
      h2 {
        font-size: 48px;
        font-weight: 500;
        font-stretch: normal;
        font-style: normal;
        line-height: 1.42;
        letter-spacing: -1.92px;
        text-align: left;
        color: #4d1c86;
      }
      p {
        margin-top: 10px;
        font-size: 18px;
        font-weight: normal;
        font-stretch: normal;
        font-style: normal;
        line-height: 1.56;
        letter-spacing: -0.36px;
        text-align: left;
        color: #222222;
      }
      .form-wrapper {
        width: 570px;
        margin-top: 40px;
        padding: 36px 40px 40px;
        box-shadow: 10px 10px 10px 0 rgba(0, 0, 0, 0.1);
        background-color: #ffffff;

        form {
          display: table;

          fieldset {
            width: 340px;
            float: left;

            input {
              width: 100%;
              padding: 10px 0;
              border-bottom: 2px solid #cccccc;
              font-size: 16px;
              font-weight: 300;
              font-stretch: normal;
              font-style: normal;
              letter-spacing: -0.64px;
              text-align: left;
              color: #222222;

              &:last-child {
                margin-top: 16px;
              }

              &:focus {
                outline: none;
                border-bottom: 2px solid #4d1c86;
              }
            }
          }

          button {
            width: 120px;
            height: 108px;
            margin-left: 29px;
            background-color: #4d1c86;
            font-size: 16px;
            font-weight: normal;
            font-stretch: normal;
            font-style: normal;
            letter-spacing: -0.32px;
            text-align: center;
            color: #ffffff;
            float: left;

            &:hover {
              background-color: #360570;
            }
          }
        }

        .form-desc {
          margin-top: 40px;
          font-size: 14px;
          font-weight: normal;
          font-stretch: normal;
          font-style: normal;
          letter-spacing: -0.56px;
          text-align: left;
          color: #666666;
        }

        .form-contact {
          margin-top: 8px;
          font-weight: normal;
          font-stretch: normal;
          font-style: normal;
          letter-spacing: normal;
          color: #222222;

          strong {
            font-size: 14px;
            font-weight: 500;
            letter-spacing: -0.56px;
          }

          a {
            margin-left: 10px;
            font-size: 15px;
          }
        }
      }
    }
  }

  .intro-item-wrapper {
    width: 1200px;
    margin: 60px auto 90px;

    section {
      width: 25%;
      display: inline-block;

      h3 {
        font-size: 20px;
        font-weight: bold;
        font-stretch: normal;
        font-style: normal;
        line-height: 1.8;
        letter-spacing: -0.4px;
        text-align: left;
        color: #222222;
        margin-top: 20px;
        margin-bottom: 6px;
      }

      p {
        font-size: 16px;
        font-weight: normal;
        font-stretch: normal;
        font-style: normal;
        line-height: 1.63;
        letter-spacing: -0.32px;
        text-align: left;
        color: #666666;
      }

      i {
        background: url('/assets/images/icon/target-icon.png') no-repeat left top;
        display: inline-block;
        height: 48px;
        width: 48px;

        &.ico01 {
          background-image: url('/assets/images/icon/target-icon.png');
        }
        &.ico02 {
          background-image: url('/assets/images/icon/pie-chart-icon.png');
        }
        &.ico03 {
          background-image: url('/assets/images/icon/report-icon.png');
        }
        &.ico04 {
          background-image: url('/assets/images/icon/communication-icon.png');
        }
      }
    }
  }
}
