.service-container {
  width: 100%;
  margin: 0 auto;

  .heading3 {
    font-size: 22px;
    line-height: 1.29;
    letter-spacing: -0.56px;
    font-weight: 700;
    margin: 40px 0 30px 0;
    &:first-child {
      margin-top: 0;
    }

    & + h4 {
      margin-top: 30px;
    }
  }

  .heading4 {
    font-size: 18px;
    font-weight: 700;
    line-height: 1.3;
    letter-spacing: -0.4px;
    margin: 40px 0 10px 0;

    &:first-child {
      margin-top: 0;
    }
  }

  p {
    color: #222;
    font-size: 14px;
    line-height: 2;
    letter-spacing: -0.32px;
    & + p {
      margin-top: 14px;
    }
  }

  table {
    margin: 20px 0 40px;
    width: 100%;
    th {
      font-size: 18px;
      font-weight: 700;
    }
    td {
      font-size: 14px;
      strong {
        font-size: 18px;
        font-weight: bold;
      }
    }
  }

  .mt20 {
    margin-top: 20px;
  }
  .mb20 {
    margin-bottom: 20px;
  }

  &.small {
    .heading3 {
      font-size: 16px;
    }

    .heading4 {
      font-size: 14px;
      margin-top: 30px;
    }

    p {
      line-height: 24px;
      letter-spacing: -0.28px;
    }
    table {
      margin-bottom: 20px;
      th {
        font-size: 14px;
      }
    }
  }

  &.small,
  &.normal {
    li {
      color: #222;
      font-size: 14px;
      display: block;
      padding: 0;
      border: 0;
      line-height: 2;
      letter-spacing: -0.32px;
      dt,
      dd {
        font-size: 14px;
        padding: 0;
        margin: 0;
        font-weight: normal;
        line-height: 2;
        letter-spacing: -0.32px;
      }
    }
  }
}
