.recomment-wrap {
  &:before {
    content: '└';
    display: inline;
    color: #cccccc;
    font-weight: bold;
  }

  &:not(:last-child) {
    border-bottom: solid 1px #dddddd;
  }

  .comment-date {
    font-size: 14px;
    line-height: 1.71;
    letter-spacing: -0.28px;
    color: #666666;
    margin-left: 10px;
  }

  .comment-time:before {
    content: '|';
    margin: 0 10px 2px 10px;
    color: #cccccc;
    font-size: 12px;
  }
}
