.service-container {
  width: 100%;
  margin: 0 auto;

  li {
    display: flex;
    align-items: center;
    border-bottom: 1px solid #cccccc;
    padding: 40px 0;
  }
  li div {
    width: 100%;
    font-size: 16px;
    line-height: 1.63;
    letter-spacing: -0.32px;
    color: #222222;
  }
  dt {
    font-size: 20px;
    font-weight: bold;
    line-height: 1.3;
    letter-spacing: -0.4px;
    padding-bottom: 10px;
  }
  dd {
    line-height: 1.63;
    letter-spacing: -0.32px;
    padding-bottom: 40px;
  }
}

.terms-contents-container {
  width: 1200px;
  margin-left: auto;
  margin-right: auto;
}
