.progress-container {
  overflow: hidden;
  float: left;

  li {
    width: 100%;
    float: left;
  }
  .progress-bar-updown {
    position: relative;
    width: 100%;
  }
  .progress-bar-updown > ul {
    margin: 0 auto;
    height: 55px;
  }
  .progress-bar {
    position: relative;
    margin: 10px 0;
    height: 40px;
    font-size: 16px;
    font-weight: bold;
    line-height: 40px;
    letter-spacing: -0.32px;
    text-align: center;
    color: #ffffff;
    z-index: 1;
    background-size: contain;
  }
  .progress-bar-updown > ul > li {
    width: 100%;
    height: 20px;
    margin: 0 5px;
    font-size: 14px;
    position: relative;
  }
  .progress-bar-updown > ul > li > p {
    width: 220px;
    position: absolute;
  }
  p.bar-upper:before {
    display: block;
    position: absolute;
    left: -10px;
    top: 7px;
  }
  p.bar-under:before {
    display: block;
    position: absolute;
    left: -10px;
    top: -47px;
    transform: rotate(180deg);
  }
  .diamond:before {
    content: url(../../../assets/images/icon/donation-diamond-stick.png);
  }
  .rectangle:before {
    content: url(../../../assets/images/icon/donation-rectangle-stick.png);
  }
  .circle:before {
    content: url(../../../assets/images/icon/donation-circle-stick.png);
  }
}
