.mypage-container {
  padding-top: 40px;
  padding-bottom: 120px;
  width: 1200px;
  margin: 0 auto;

  .label {
    font-size: 20px;
    font-weight: bold;
    letter-spacing: -0.4px;
    line-height: 24px;
    display: block;
    &.required::before {
      content: '*';
      color: #e66526;
      font-weight: 600;
      vertical-align: middle;
      font-size: 20px;
    }
  }

  .col1 {
    width: 315px;
    display: inline-block;
    vertical-align: top;
    & > h3 {
      font-weight: bold;
      font-size: 28px;
      line-height: 1.29;
      letter-spacing: -0.56px;
      color: #222222;
    }
  }

  .col2 {
    width: 885px;
    display: inline-block;
  }

  form input,
  form select {
    width: 420px;
    height: 52px;
    padding: 14px;
    border: 1px solid #cccccc;
    background-color: #ffffff;
    font-size: 16px;
    font-weight: normal;
    font-stretch: normal;
    font-style: normal;
    line-height: 1;
    letter-spacing: -0.32px;
    color: #222222;
  }

  form input::placeholder,
  form select::placeholder {
    color: #888888;
  }

  form input.mobile {
    width: 84px;
  }

  form select {
    background: url('/assets/images/icon/arrow-down.png') no-repeat 95% 50%;
    border-radius: 0px;
    -webkit-appearance: none;
    -moz-appearance: none;
    appearance: none;
  }

  form input.email,
  form select.email {
    width: 255px;
  }

  form select.email {
    margin-left: 10px;
  }

  form fieldset {
    margin: 20px 0;
    position: relative;
  }

  form fieldset:first-child {
    margin-top: 0;
  }

  form fieldset:first-child ul {
    padding-top: 0;
  }

  form fieldset:first-child ul li:first-child {
    margin-top: 0px;
  }

  form fieldset ul {
    padding: 20px 0;
  }

  form fieldset ul li {
    margin-top: 30px;
    &:first-of-type {
      margin-top: 0;
    }
  }

  form fieldset ul li .half {
    display: inline-block;
  }

  form fieldset ul li .half:last-child {
    margin-left: 45px;
  }

  form fieldset ul li label {
    margin-bottom: 10px;
    font-weight: bold;
    font-size: 20px;
    line-height: 26px;
    letter-spacing: -0.4px;
    color: #222222;
    display: block;
  }

  form fieldset ul li label .text-orange {
    color: #e66526;
    font-weight: 600;
    vertical-align: middle;
    font-size: 20px;
  }

  form fieldset ul li span {
    font-size: 16px;
    font-weight: normal;
    line-height: 1.63;
    letter-spacing: -0.32px;
    color: #222222;
  }

  .helper-txt {
    margin: 10px 0 0 0;
    font-size: 14px;
    font-weight: normal;
    font-stretch: normal;
    font-style: normal;
    line-height: 1.86;
    letter-spacing: -0.28px;
    text-align: left;
    color: #888888;
  }

  .required-text {
    color: #e66526;
    text-align: right;
    font-size: 14px;
    letter-spacing: -0.28px;
    line-height: 20px;
  }

  .helper-txt.error {
    color: #ff0000;
  }

  span.hyphen,
  span.email {
    margin: 0 10px;
    display: inline-block;
  }

  .btn-row {
    margin-top: 30px;
    text-align: center;
  }

  form button {
    width: 100px;
    height: 52px;
    font-size: 16px;
    font-weight: normal;
    font-stretch: normal;
    font-style: normal;
    line-height: 2.25;
    letter-spacing: -0.32px;
    text-align: center;
  }

  form button.cancel {
    color: #e66526;
    border: solid 1px #e66526;
  }

  form button.submit {
    margin-left: 10px;
    color: #ffffff;
    background-color: #e66526;
  }
}
