.summary-container {
  width: 1200px;
  margin: 0 auto;
  padding-top: 40px;
  padding-bottom: 120px;

  .summary-container--quater {
    font-size: 14px;
    letter-spacing: -0.28px;
    text-align: right;
    color: #e66526;
  }
  .summary-container--list-item {
    border-bottom: 1px solid #cccccc;
    padding: 40px 0;
    overflow: hidden;
  }
  .summary-container--list-item:last-child {
    border: none;
  }
  .summary-container--list-item > h4 {
    width: 315px;
    font-size: 28px;
    line-height: 1.29;
    letter-spacing: -0.56px;
    font-weight: bold;
    float: left;
  }
  .summary-container--list-item > div,
  .summary-container--list-item > dl {
    width: 885px;
    display: inline-block;
  }
  .summary-container--list-item > div > p {
    height: 100%;
    vertical-align: middle;
  }
  .graph-container {
    width: 435px;
    float: left;
    position: relative;
    right: 25px;
  }
  .graph-container--meta {
    text-align: center;
    position: absolute;
    top: 48%;
    left: 50%;
    transform: translate(-50%, -50%);
  }
  .graph-container--meta--title {
    font-size: 14px;
    font-weight: normal;
    line-height: 1.86;
    letter-spacing: -0.28px;
    color: #222222;
  }
  .graph-container--meta--budget {
    font-size: 24px;
    font-weight: normal;
    line-height: 1.08;
    letter-spacing: -0.48px;
    color: #222222;
  }
  .budget-container {
    width: 450px;
    float: right;
  }
  .budget-container hr {
    margin-bottom: 20px;
    color: #dddddd;
  }
  .budget-container--status-flag {
    width: 30px;
    height: 14px;
    margin-right: 6px;
    display: inline-block;
  }
  .budget-container--status-flag.color-1 {
    background-color: #e66526;
  }
  .budget-container--status-flag.color-2 {
    background-color: #7b7572;
  }
  .budget-container--status-flag.color-3 {
    background-color: #4d1c86;
  }
  .summary-container--list-item dt {
    display: flex;
    justify-content: flex-start;
    width: 100%;
    font-size: 20px;
    font-weight: bold;
    line-height: 1.3;
    letter-spacing: -0.4px;
    color: #222222;
    margin-bottom: 10px;
  }
  .summary-container--list-item dd {
    margin-bottom: 20px;
  }
  .summary-container--list-item > dl > dd:last-child {
    margin-bottom: 0;
  }
  .orange-title {
    color: #e66526;
    font-size: 16px;
  }
  .gray-title {
    color: #7b7572;
    font-size: 16px;
  }
  .gray-tag {
    display: inline-block;
    height: 44px;
    margin: 0px 6px 6px 0px;
    padding: 10px 15px;
    border-radius: 5px;
    background-color: #f5f5f5;
    line-height: 1.63;
    letter-spacing: -0.32px;
    text-align: center;
    color: #222222;
  }
  .gray-caption {
    font-size: 14px;
    font-weight: normal;
    font-style: normal;
    line-height: 1.86;
    letter-spacing: -0.28px;
    text-align: right;
    color: #666666;
    margin-left: auto;
  }
  .total-container {
    border: 1px solid #e66526;
    overflow: hidden;
  }
  .total-container p {
    border-right: 1px solid #cccccc;
    padding: 0 30px;
    margin: 30px 0;
    width: 33%;
    float: left;
  }
  .total-container p:last-child {
    border: none;
  }
  .summary-tbl {
    width: 100%;
    margin-bottom: 30px;
  }
  .summary-tbl td {
    height: 56px;
  }
  .summary-tbl td.border-right {
    border-right: 1px solid #dddddd;
  }
  .summary-tbl td.bold {
    font-weight: bold;
  }
  .summary-tbl td.orange {
    font-size: 16px;
    color: #e66526;
  }
  iframe {
    width: 100%;
  }
}
