.nav-container {
  border-top: 1px solid #dddddd;
  border-bottom: 1px solid #dddddd;
  width: 100%;
  height: 60px;
  background-color: #ffffff;

  nav {
    width: 1200px;
    height: 100%;
    margin: 0 auto;

    ul li {
      width: 25%;
      font-size: 18px;
      color: #666666;
      text-align: center;

      &.selcted-line {
        border-bottom: 3px solid #e66526;
        height: calc(100% + 4px);
      }

      .selected-text {
        font-weight: bold;
        color: #e66526;
        margin-top: 3px;
      }
    }
  }
}
