.empty-report-container {
  background-color: #f5f5f5;
  padding: 120px 0;

  .emtpy-report-container-inner {
    background-color: #ffffff;
    width: 1200px;
    margin: 0 auto;
    text-align: center;
    padding: 70px 0;
    img {
      margin: 0 auto;
    }
    strong {
      color: #888888;
      line-height: 55px;
    }
  }
}

.report-container {
  background-color: #f5f5f5;

  .report-container-inner {
    width: 1200px;
    margin: 0 auto;
    select {
      border: 1px solid #e66526;
    }
  }

  .report-doc-wrap {
    margin-top: 20px;
  }

  .attach-wrap {
    width: 100%;
    padding: 40px;
    display: table;
    background-color: #ffffff;

    h3 {
      width: 275px;
      font-size: 28px;
      font-weight: bold;
      line-height: 1.29;
      letter-spacing: -0.56px;
      text-align: left;
      vertical-align: top;
      color: #222222;
      display: table-cell;
    }

    div {
      width: 845px;
      box-sizing: border-box;
    }

    .btn-download {
      height: 42px;
      margin: 0 0 10px 0;
      float: left;
      padding: 9px 14px 10px 42px;
      border: solid 1px #222222;
      background-color: #ffffff;
      font-size: 16px;
      font-weight: 500;
      line-height: 1.63;
      letter-spacing: -0.32px;
      color: #222222;
      background-image: url(/assets/images/icon/ic-download.svg);
      background-size: 18px 18px;
      background-repeat: no-repeat;
      background-position: left 14px top 12px;
    }

    .btn-download {
      margin-left: 10px;
    }
  }
}
