.notice-box-wrap {
  position: relative;
  .btn-alarm {
    border: 1px solid #eee;
    background-color: #eee;
    width: 60px;
    height: 60px;
    position: relative;

    &.active {
      border: 1px solid #ddd;
      border-bottom: 0;
      background-color: #fff;

      & + .alarm-list-wrap {
        display: block;
      }
    }
  }
  .new-marker {
    width: 6px;
    height: 6px;
    border-radius: 3px;
    background-color: #e66526;
    position: absolute;
    right: 12px;
    top: 17px;
  }

  .ico-alarm {
    background: url('/assets/images/icon/bell-icon.png') left top no-repeat;
    background-size: contain;
    display: inline-block;
    width: 23px;
    height: 25px;
    vertical-align: middle;
  }

  .alarm-list-wrap {
    position: absolute;
    width: 363px;
    right: 0;
    display: none;
    transition: max-height ease-in 0.3s;
    overflow: hidden;

    .alarm-list-inner {
      background-color: #fff;
      border: 1px solid #ddd;
      padding-top: 5px;
      width: 100%;
      padding: 5px 20px 0;
    }

    .alarm-list {
      .alarm-item {
        width: 100%;
        height: 49px;
        border-bottom: solid 1px #dddddd;
        display: flex;
        justify-content: space-between;
        align-items: center;

        .empty {
          text-align: center;
          font-size: 14px;
          line-height: 1.43;
          letter-spacing: -0.28px;
          color: #888888;

          p {
            margin: 0;
          }
        }

        button {
          width: calc(100% - 64px);
          font-size: 14px;
          line-height: 1.43;
          letter-spacing: -0.28px;
          text-align: left;
          text-overflow: ellipsis;
          white-space: nowrap;
          overflow: hidden;
          color: #222222;
          cursor: pointer;
        }

        span {
          font-family: 'Lato';
          font-size: 12px;
          line-height: 1.5;
          text-align: right;
          color: #666666;
        }
      }
    }

    .view-more {
      display: inline-block;
      text-align: center;
      padding: 20px 0;
      font-size: 14px;
      font-weight: bold;
      line-height: 1.43;
      letter-spacing: -0.28px;
      color: #7b7572;
      width: 100%;
    }
  }
}

.notice-popover {
  .alarm-list {
    padding-top: 5.5px;

    .alarm-item {
      width: 100%;
      padding: 14.5px 0;
      border-bottom: solid 1px #dddddd;
      display: table;

      .empty {
        text-align: center;
        font-size: 14px;
        line-height: 1.43;
        letter-spacing: -0.28px;
        color: #888888;

        p {
          margin: 0;
        }
      }

      button {
        width: 245px;
        margin-right: 10px;
        font-size: 14px;
        line-height: 1.43;
        letter-spacing: -0.28px;
        text-align: left;
        text-overflow: ellipsis;
        white-space: nowrap;
        overflow: hidden;
        color: #222222;
        cursor: pointer;
        display: block;
      }

      span {
        width: 78px;
        font-family: 'Lato';
        font-size: 12px;
        line-height: 1.5;
        text-align: right;
        color: #666666;
        display: table-cell;
      }
    }
  }

  .view-more {
    text-align: center;
    padding: 20px;

    a {
      font-size: 14px;
      font-weight: bold;
      line-height: 1.43;
      letter-spacing: -0.28px;
      color: #7b7572;
    }
  }
}
