.project-select {
  width: 455px;
  margin: 35px 30px 18px 60px;
  position: relative;

  .select-title {
    padding-right: 24px;
    text-align: left;
    height: 34px;
    width: 100%;

    &::after {
      background: transparent url('/assets/images/icon/arrow-down.png') no-repeat;
      content: '';
      display: inline-block;
      height: 24px;
      position: absolute;
      transition: all ease-in 0.3s;
      top: 5px;
      right: 0;
      width: 24px;
    }

    span {
      color: #4d1c86;
      font-size: 18px;
      font-weight: 500;
      letter-spacing: -0.36px;
    }
    &.open {
      &::after {
        transform: rotate(-180deg);
      }

      & + ul {
        height: auto;
        max-height: 100vh;
        display: block;
      }
    }
  }

  ul {
    display: none;
    border: 1px solid #ddd;
    background-color: #fff;
    padding: 13px 20px;
    box-shadow: 0 3px 4px 0 rgba(0, 0, 0, 0.06);
    height: 0;
    overflow: hidden;
    height: 0;
    max-height: 0;
    transition: all 0.4s ease-in-out;
    position: absolute;
    width: 100%;
    z-index: 1;
    margin-top: -1px;

    li {
      display: block;
      button {
        color: #666;
        text-align: left;
        width: 100%;
        display: inline-block;
        font-size: 16px;
        line-height: 38px;
        height: 38px;
        letter-spacing: -0.32px;
        &:hover,
        &:focus {
          color: #4d1c86;
        }

        &[aria-selected='true'] {
          color: #4d1c86;
        }
      }
    }
  }
}
