.lnb {
  width: 340px;
  font-size: 14px;
  letter-spacing: -0.28px;
  padding-top: 20px;

  select {
    width: 500px;
    height: 30px;
    color: #4d1c86;
  }
  option {
    color: #666666;
  }

  .btn-mypage {
    display: flex;
    align-items: center;

    i {
      background: url('/assets/images/icon/user-icon.png') no-repeat;
      background-size: 100% auto;
      display: inline-block;

      margin-right: 4px;
      height: 15px;
      width: 15px;
      vertical-align: middle;
    }

    span {
      font-size: 14px;
      font-weight: 500;
      color: #222;
      letter-spacing: -0.28px;
      vertical-align: middle;
    }
  }
}
