footer {
  position: relative;
  width: 100%;
  padding: 40px 0 50px 0;
  background-color: #444444;
  text-align: center;

  nav a {
    font-size: 16px;
    font-weight: normal;
    line-height: 1.63;
    letter-spacing: -0.32px;
    color: #ffffff;

    &:not(:first-child) {
      margin-left: 30px;
    }

    &.active {
      font-size: 16px;
      font-weight: bold;
      line-height: 1.63;
      letter-spacing: -0.32px;
      color: #ffffff;
    }
  }

  ul {
    margin-top: 20px;
    margin-bottom: 10px;

    li {
      font-size: 14px;
      font-weight: normal;
      line-height: 1.71;
      letter-spacing: -0.28px;
      color: #aaaaaa;
      display: inline;

      &:not(:first-child):before {
        content: '|';
        margin: 0 10px;
      }
    }
  }

  small {
    font-size: 14px;
    font-weight: normal;
    line-height: 1.71;
    letter-spacing: -0.28px;
    color: #aaaaaa;
    display: block;
  }

  .mark {
    display: inline-block;
    margin-top: 15px;
  }
}
