.infographic-card {
  width: 378px;
  height: 535px;
  background-repeat: no-repeat;
  background-size: 380px 351px;
  background-position-y: -2px;
  position: relative;
  display: inline-block;
  background-color: #ffffff;
  box-shadow: 3px 3px 8px 0 rgba(0, 0, 0, 0.5);
  opacity: 0;
  animation: fadein 0.8s ease-out, translateY 0.8s ease-out;
  -moz-animation: fadein 0.8s ease-out, translateY 0.8s ease-out; /* Firefox */
  -webkit-animation: fadein 0.8s ease-out, translateY 0.8s ease-out; /* Safari and Chrome */
  -o-animation: fadein 0.8s ease-out, translateY 0.8s ease-out; /* Opera */
  animation-fill-mode: forwards;

  img {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    opacity: 0;
    animation: fadein 0.8s ease-out, translateY 0.8s ease-out;
    -moz-animation: fadein 0.8s ease-out, translateY 0.8s ease-out;
    -webkit-animation: fadein 0.8s ease-out, translateY 0.8s ease-out;
    -o-animation: fadein 0.8s ease-out, translateY 0.8s ease-out;
    animation-fill-mode: forwards;
  }

  h4 {
    margin: 0;
    font-size: 20px;
    font-weight: bold;
    line-height: 1.3;
    letter-spacing: -0.4px;
    text-align: left;
    color: #222222;
    position: absolute;
    bottom: 106px;
    left: 30px;
  }

  .value-row {
    position: absolute;
    bottom: 50px;
    left: 30px;

    .value {
      font-family: 'Lato';
      font-size: 38px;
      font-weight: bold;
      line-height: 0.68;
      text-align: left;
      color: #e66526;
    }

    .unit {
      margin-left: 4px;
      font-size: 20px;
      color: #e66526;
    }
  }
}
