.communication-container {
  width: 1200px;
  margin: 0 auto;
  padding-top: 30px;
  .total-text {
    font-size: 20px;
    font-weight: bold;
    font-stretch: normal;
    font-style: normal;
    line-height: 1.3;
  }
  .total-text b {
    color: #e66526;
  }
  tbody > tr:hover {
    background-color: #efefef;
  }
  select {
    background-position-x: 90%;
  }

  .basic-input-text + .basic-input-button {
    border-left: 0;
  }
}
