.report-wrap {
  width: 1200px;
  padding: 40px 40px 0;
  box-sizing: border-box;
  background-color: #ffffff;

  .report-wrap--header {
    padding-bottom: 20px;
    border-bottom: 1px solid #222222;
  }
  .report-wrap--header--donor-name {
    margin: 0;
    font-size: 20px;
    font-weight: bold;
    line-height: 1.8;
    letter-spacing: -0.4px;
    color: #e66526;
  }
  .report-wrap--header--meta-wrap {
    width: 100%;
    margin-top: 2px;
  }
  .report-wrap--header--title {
    margin: 0;
    font-size: 42px;
    font-weight: bold;
    line-height: 1;
    letter-spacing: -0.84px;
    text-align: left;
    color: #222222;
    width: 760px;
    word-break: keep-all;
  }
  .report-wrap--header--meta-wrap > p {
    height: 100%;
    margin-top: 20px;
    text-align: right;
    vertical-align: middle;
    font-size: 14px;
    line-height: 1.71;
    letter-spacing: -0.28px;
    color: #222222;
  }
  .report-wrap--header--meta-wrap > p > span:last-child::before {
    margin: 0 14px;
    content: '|';
    color: #aaaaaa;
  }
  .report-wrap--body--list {
    list-style: none;
    margin: 0;
    padding: 0;
  }
  .report-wrap--body--list--row {
    width: 100%;
    padding: 40px 0;
    display: table;
    border-bottom: 1px solid #cccccc;
  }
  .report-wrap--body--list--row > h3 {
    width: 275px;
    font-size: 28px;
    font-weight: bold;
    line-height: 1.29;
    letter-spacing: -0.56px;
    text-align: left;
    vertical-align: top;
    color: #222222;
    display: table-cell;
  }
  .report-wrap--body--list--row > div {
    width: 845px;
  }
  .aggr-wrap {
    width: 100%;
    display: table;
  }
  .aggr-wrap--summary {
    width: 360px;
    padding: 20px;
    border: 1px solid #dddddd;
    display: table-cell;
  }
  .aggr-wrap--summary table {
    width: 100%;
    border-collapse: collapse;
    border: none;
  }
  .aggr-wrap--summary table tr > th {
    font-size: 16px;
    font-weight: normal;
    line-height: 1.63;
    letter-spacing: -0.32px;
    color: #222222;
    border-top: none;
    padding: 0 0 10px 0;
  }
  .aggr-wrap--summary table tr > td {
    font-size: 16px;
    font-weight: bold;
    letter-spacing: normal;
    text-align: right;
    color: #222222;
    border-top: none;
    padding: 0 0 10px 0;
  }
  .aggr-wrap--summary table tr:last-child th,
  .aggr-wrap--summary table tr:last-child td {
    border-top: solid 1px #dddddd;
    color: #e66526;
    padding: 10px 0 0 0;
  }
  .aggr-wrap--progress-wrap {
    width: 485px;
    padding-left: 60px;
    display: table-cell;
    vertical-align: top;
  }
  .aggr-wrap--progress-wrap > div > h5 {
    margin: 0;
    font-size: 20px;
    font-weight: bold;
    line-height: 1.3;
    letter-spacing: -0.4px;
    text-align: left;
    color: #222222;
  }
  .aggr-wrap--progress-wrap > div:last-child {
    border: 1px solid #e66526;
    font-size: 16px;
    padding: 12px 17px;
  }
  .aggr-wrap--progress-wrap > div:last-child > h5 {
    color: #666666;
    font-size: 16px;
  }
  .aggr-wrap--progress-wrap > div:last-child > p {
    color: #666666;
  }
  .aggr-wrap--progress-wrap--progressbar {
    width: 100%;
    height: 14px;
    margin: 10px 0 20px;
    background-color: #e8e8e8;
    position: relative;
    text-align: left;
    overflow: hidden;

    .progress {
      display: inline-block;
      height: 100%;
      width: 0;
      background: url('/assets/images/background/acc-progress.svg') left top repeat-x;
      background-size: cover;
      top: 0;
      left: 0;
      bottom: 0;
      position: absolute;
    }
  }
  .aggr-wrap--progress-wrap--progressbar img {
    height: 100%;
    position: absolute;
    left: 0;
    top: 0;
    bottom: 0;
    object-fit: cover;
    display: inline-block;
  }
  .aggr-wrap--progress-wrap p {
    margin: 0;
    vertical-align: top;
  }
  .aggr-wrap--progress-wrap--ratio {
    margin: 0;
    font-family: 'Lato';
    font-size: 36px;
    text-align: right;
    color: #e66526;
  }
  .aggr-wrap--progress-wrap--unit {
    margin-left: 6px;
    font-family: 'Lato';
    font-size: 24px;
    text-align: right;
    color: #e66526;
  }
  .tbl-breakdown {
    border-collapse: collapse;
  }
  .tbl-breakdown th {
    height: 44px;
    text-align: center;
    background-color: #f5f5f5;
    border-top: 2px solid #cccccc;
  }
  .tbl-breakdown th:nth-child(1) {
    width: 80px;
  }
  .tbl-breakdown th:nth-child(2) {
    width: 240px;
  }
  .tbl-breakdown th:nth-child(3) {
    width: 175px;
  }
  .tbl-breakdown th:nth-child(4) {
    width: 120px;
  }
  .tbl-breakdown th:nth-child(5) {
    width: 230px;
  }
  .tbl-breakdown td {
    height: 56px;
    text-align: center;
    border-top: 1px solid #dddddd;
  }
  .tbl-breakdown > tbody > tr:last-child > td {
    border-bottom: 2px solid #cccccc;
  }
  .additional-info-wrap {
    padding: 30px;
    border: 1px solid #dddddd;

    a {
      text-decoration: underline;
      color: #0782c1;

      &:visited {
        color: #660099;
      }
    }
    ul {
      display: block;
      margin-block-start: 1em;
      margin-block-end: 1em;
      margin-inline-start: 0px;
      margin-inline-end: 0px;
      padding-inline-start: 40px;
      display: list-item;
      text-align: -webkit-match-parent;
      li {
        list-style: disc;
      }
    }
    ol {
      display: block;
      margin-block-start: 1em;
      margin-block-end: 1em;
      margin-inline-start: 0px;
      margin-inline-end: 0px;
      padding-inline-start: 40px;
      li {
        list-style: decimal;
      }
    }
  }
}
