.donation-infograph {
  width: 100%;
  height: 655px;
  padding-top: 60px;
  background-image: linear-gradient(to bottom, #320f5a, #4d1c86);
  text-align: center;

  .infograph-content {
    width: 1200px;
    margin: 0 auto;
  }
}

.donation-container {
  width: 1200px;
  margin: 0 auto;
  padding-top: 30px;
  margin-bottom: 60px;
  & > ul > li {
    display: flex;
    align-items: center;
    border-bottom: 1px solid #cccccc;
    padding: 40px 0;
  }
  & > ul > li:last-child {
    border: none;
    display: block;
    h4 {
      padding-bottom: 20px;
    }
    div {
      width: 100%;
      display: flex;
      justify-content: center;
    }
  }
  & > ul > li > h4 {
    align-self: flex-start;
    width: 300px;
    font-size: 28px;
    line-height: 1.29;
    letter-spacing: -0.56px;
    font-weight: bold;
  }
  & > ul > li > div {
    width: 900px;
    font-size: 16px;
    line-height: 1.63;
    letter-spacing: -0.32px;
    color: #222222;
  }
  .orange-title {
    color: #e66526;
    font-size: 14px;
    line-height: 1.86;
    letter-spacing: -0.28px;
  }
  .colspan-border-right {
    border-right: 1px solid #ddd;
  }
  .amount-right {
    width: fit-content;
    margin-left: auto;
    margin-right: 35%;
  }

  table {
    width: 100%;
  }
}
